<style lang="less" scoped>
.wrap-base {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0 2%;
  background-color: #04183b;
}

::v-deep .el-main {
  padding: 0;
}

// 左侧拉力统计
.pull-statistics {
  font-size: 14px;
  background: url(../assets/images/petrify/ll.png) top no-repeat;

  figcaption {
    padding-top: 6px;
    text-align: center;
  }

  li {
    margin-top: 4px;
    padding: 4px;
    border: solid 1px #2c9dd9;
    border-radius: 6px;
  }

  span:first-of-type {
    box-sizing: border-box;
    width: 200px;
    padding-left: 30px;
    background-color: #09608d;
    border-radius: 6px;
  }

  span.flex-1 {
    margin-left: 16%;
  }
}

// 左侧管线统计
.pipeline {
  width: 410px;
  height: 180px;
  font-size: 14px;
  background: url(../assets/images/petrify/pipeline.png) 0 0 no-repeat;
  background-size: 100% 100%;
  position: relative;

  figcaption {
    position: absolute;
    left: 40px;
    top: 10px;
  }

  ul {
    margin: 38px 24px;
  }

  li {
    font-size: 10px;
  }
}

// 左侧环形图
.circle-pic {
