    width: 100px;
    height: 100px;
  }
}

// 左侧氮气统计
.nitrogen {
  height: 200px;
  width: 100%;
  background: url(../assets/images/petrify/氮气系统.png) 0 0 no-repeat;
  background-size: 100% 100%;

  ul {
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
  }

  li {
    width: 90px;
    font-size: 12px;
    line-height: 14px;
    text-align: center;

    &:first-of-type {
      margin-top: -2px;
    }
    &:nth-of-type(2) {
      margin-top: -24px;
    }
    &:nth-of-type(3) {
      margin-top: -22px;
    }
    &:last-of-type {
      margin-top: -24px;
    }
  }
}

#nitrogen-chart {
  width: 254px;
  height: 130px;
}

// 左侧其他项目
.other-items {
